.backdrop {
  width: 100%;
  height: 100%;
  z-index: 100;
  background: linear-gradient(-45deg, #ffeb3b, #74efff, #9c27b0) 0 0 / 400% 400%;
  padding: 30px;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  animation: gradient-bg 15s infinite;
  display: flex;
  position: fixed;
  inset: 0;
}

@keyframes gradient-bg {
  0% {
    background-position: 0%;
  }

  50% {
    background-position: 100%;
  }

  100% {
    background-position: 0%;
  }
}

.hidden-submit {
