.modal {
  max-width: 579px;
  width: 335px;
  border: 2px solid var(--sign-up-border-color);
  background: var(--sign-up-bg);
  height: 516px;
  cursor: default;
  border-radius: 18px;
  padding: 80px 40px 40px;
  position: absolute;
  top: 50vh;
  left: 50vw;
  transform: translate(-50%, -50%);
}

.modal-close {
  cursor: pointer;
  background-color: #0000;
  border: none;
  position: absolute;
  top: 24px;
  right: 24px;
}

.modal-form {
  flex-direction: column;
  gap: 28px;
  display: flex;
}

.modal-input {
  width: 100%;
  border: 2px solid var(--sign-up-border-color);
  color: var(--text-color);
  background-color: #0000;
  border-radius: 40px;
  align-items: center;
  padding: 20px 0 20px 40px;
  display: flex;
}

.modal-input::placeholder {
  color: var(--text-color);
  letter-spacing: -.18px;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.33;
}

.modal-form__submit {
  width: 100%;
  text-align: center;
  background: var(--sign-up-btn-bg);
  color: var(--sign-up-btn-text-color);
  letter-spacing: -.18px;
  text-transform: uppercase;
  cursor: pointer;
  border-radius: 40px;
  padding: 20px 0;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.33;
}

.modal-toggle {
  justify-content: center;
  gap: 20px;
  margin-top: 14px;
  display: flex;
}

.modal-toggle__el {
  color: #4f2ee8;
  letter-spacing: -.18px;
  text-transform: uppercase;
  color: var(--sign-up-text-color);
  background-color: #0000;
  border: none;
  font-size: 18px;
  line-height: 1.33;
  text-decoration-line: none;
  transition: color .25s ease-in-out;
}

.userbar-btn__icon-close {
  fill: var(--sign-up-icon-color);
}

.modal-toggle__el:hover {
  color: #4f2ee8;
}

.modal-toggle__el--current {
  color: #4f2ee8;
  text-decoration: underline;
}

.modal-toggle--signIn {
  display: none;
}

.modal-form__label {
  position: relative;
}

.modal-form__icon {
  fill: var(--sign-up-icon-color);
  width: 28px;
  height: 28px;
  position: absolute;
  top: 18px;
  right: 32px;
  transform: translate(-50%);
}

